import { animate, state, style, transition, trigger } from "@angular/animations";
import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatSelectChange } from "@angular/material/select";
import { MatTableDataSource } from "@angular/material/table";
import { Observable } from "rxjs";
import { IOwners } from "./../../interfaces/owners";

export interface IProperties {
  sn: number;
  image: string;
  name: string;
  contact: string;
  address: string;
}
@Component({
  selector: "app-active-owners",
  templateUrl: "./active-owners.component.html",
  animations: [
    trigger("detailExpand", [
      state("collapsed", style({ height: "0px", minHeight: "0", visibility: "hidden" })),
      state("expanded", style({ height: "*", visibility: "visible" })),
      transition("expanded <=> collapsed", animate("225ms cubic-bezier(0.4, 0.0, 0.2, 1)")),
    ]),
  ],
})
export class ActiveOwnersComponent implements OnInit {
  @Input() activeOwners$: Observable<IOwners[]>;
  @Output() paramChanged: EventEmitter<any> = new EventEmitter<any>();
  @Output() invitePropertyOwnerClicked: EventEmitter<null> = new EventEmitter<null>();
  dataSource = new MatTableDataSource<IOwners>();

  PROPERTIES_DATA: IProperties[] = [
    {
      sn: 1,
      image: "./assets/images/hero.jpg",
      name: "Sunrise Homes",
      contact: "977-1-423567890",
      address: "Tripura Marg, Tripureswor, Kathmandu, Nepal",
    },
    {
      sn: 2,
      image: "./assets/images/hero.jpg",
      name: "Sunrise Homes",
      contact: "977-1-423567890",
      address: "Tripura Marg, Tripureswor, Kathmandu, Nepal",
    },
    {
      sn: 3,
      image: "./assets/images/hero.jpg",
      name: "Sunrise Housing",
      contact: "977-1-423567890",
      address: "Balkumari, Koteshwor, Kathmandu, Nepal",
    },
  ];

  columnsToDisplay = ["sn", "fullname", "mobileNumber", "email", "ownerType"];
  expandedElement: IOwners | null;

  dataSourceProperties = this.PROPERTIES_DATA;
  columnsToDisplayProperties = ["sn", "image", "name", "contact", "address", "actions"];

  constructor() {}

  ngOnInit(): void {
    this.listenObservables();
  }

  listenObservables() {
    this.activeOwners$.subscribe((activeOwners) => {
      this.dataSource = new MatTableDataSource(activeOwners);
    });
  }

  onOwnerTypeChange(typeChange: MatSelectChange) {
    const param = {
      page: 1,
      type: typeChange.value,
    };
    this.paramChanged.emit(param);
  }

  handleInviteOwnerModal() {
    this.invitePropertyOwnerClicked.emit();
  }
}
