import { createReducer, on } from "@ngrx/store";
import { IInvitations } from "@shared/interfaces/invitations";
import { IPagination } from "@shared/interfaces/pagination";
import { IOwners } from "../../interfaces/owners";
import * as OwnersActions from "../actions/owners.actions";

export const ownerFeatureKey = "owners";

export interface OwnersState {
  loading: boolean;
  message: string;
  list: IOwners[];
  detail: IOwners;
  pagination: IPagination;
  error: Error;
  invitations: IInvitations[];
}

export const initialOwnersState: OwnersState = {
  list: [],
  pagination: null,
  loading: false,
  message: "",
  detail: null,
  error: null,
  invitations: [],
};

export const ownersReducer = createReducer(
  initialOwnersState,
  on(OwnersActions.loadActiveOwners, (state: OwnersState) => ({
    ...state,
    loading: true,
  })),

  on(OwnersActions.loadActiveOwnersSuccess, (state: OwnersState, action) => ({
    ...state,
    loading: false,
    list: action.data,
  })),

  on(OwnersActions.loadActiveOwnersFailure, (state: OwnersState, action) => ({
    ...state,
    loading: false,
  })),

  on(OwnersActions.loadPendingOwnerInvitations, (state: OwnersState) => ({
    ...state,
    loading: true,
  })),

  on(OwnersActions.loadPendingOwnerInvitationsSuccess, (state: OwnersState, action) => ({
    ...state,
    loading: false,
    invitations: action.data,
  })),

  on(OwnersActions.loadPendingOwnerInvitationsFailure, (state: OwnersState, action) => ({
    ...state,
    loading: false,
  })),
  on(OwnersActions.getOwnerDetails, (state: OwnersState) => ({
    ...state,
    loading: true,
  })),

  on(OwnersActions.getOwnerDetailsSuccess, (state: OwnersState, action) => ({
    ...state,
    loading: false,
    detail: action.data,
    message: action.message,
  })),

  on(OwnersActions.getOwnerDetailsFailure, (state: OwnersState, action) => ({
    ...state,
    loading: false,
    message: action.message,
  }))
);
