import { createAction, props } from "@ngrx/store";
import { IParameters } from "@shared/interfaces";
import { IUpdateInvitationStatus } from "./../../interfaces/update-invitation-status";

export const loadActiveOwners = createAction("[OWNERS] Load Active Owners", props<{ params?: IParameters }>());

export const loadActiveOwnersSuccess = createAction("[OWNERS] Load Active Owners Success", props<{ data: any }>());

export const loadActiveOwnersFailure = createAction("[OWNERS] Load Active Owners Failure", props<{ error: any }>());

export const loadPendingOwnerInvitations = createAction(
  "[OWNERS] Load Pending Invitations",
  props<{ params?: IParameters }>()
);

export const loadPendingOwnerInvitationsSuccess = createAction(
  "[OWNERS] Load Pending Owner Invitations Success",
  props<{ data: any }>()
);

export const loadPendingOwnerInvitationsFailure = createAction(
  "[OWNERS] Load Pending Owner Invitations Failure",
  props<{ error: any }>()
);

export const updateInvitationStatus = createAction(
  "[OWNERS] Update Invitation Status",
  props<{ payload: IUpdateInvitationStatus }>()
);

export const updateInvitationStatusSuccess = createAction(
  "[OWNERS] Update Invitation Status Success",
  props<{ data: any }>()
);

export const updateInvitationStatusFailure = createAction(
  "[OWNERS] Update Invitation Status Failure",
  props<{ error: any }>()
);

export const getOwnerDetails = createAction("[OWNERS] Get Owner Details", props<{ id: string }>());

export const getOwnerDetailsSuccess = createAction("[OWNERS] Get Owner Details Success", props<any>());

export const getOwnerDetailsFailure = createAction("[OWNERS] Get Owner Details Failure", props<any>());
