import { Component, OnInit, ViewChild } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { BaseComponent } from "@core/components";
import Utils from "@core/utilities/utils";
import { Actions, ofType } from "@ngrx/effects";
import { Store } from "@ngrx/store";
import { IParameters } from "@shared/interfaces";
import { IInvitations } from "@shared/interfaces/invitations";
import * as invitationsAction from "@shared/store/actions/invitations.actions";
import { Observable, Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { InvitedOwnersComponent } from "../../components/invited-owners/invited-owners.component";
import { PropertyOwnerInvitationComponent } from "./../../../../../../shared/property-owner-invitation/property-owner-invitation.component";
import { invitePropertyOwner } from "./../../../../../../shared/store/actions/invitations.actions";
import { IUpdateInvitationStatus } from "./../../interfaces/update-invitation-status";
import { loadPendingOwnerInvitations, updateInvitationStatus } from "./../../store/actions/owners.actions";

@Component({
  selector: "app-invited-owners-container",
  templateUrl: "./invited-owners-container.component.html",
})
export class InvitedOwnersContainerComponent extends BaseComponent implements OnInit {
  @ViewChild("invitedOwnersCmp") invitedOwnersCmp: InvitedOwnersComponent;
  pendingOwnerInvitations$: Observable<IInvitations[]>;

  filterParams: IParameters;
  destroyed$ = new Subject<boolean>();
  dialogRef: any;

  params = {
    page: 1,
    size: 10,
    search: null,
    ownerType: null,
    status: 0,
  };

  constructor(private store: Store<any>, public dialog: MatDialog, public invitePropertyOwnerSuccess$: Actions) {
    super();
    invitePropertyOwnerSuccess$
      .pipe(ofType(invitationsAction.invitePropertyOwnerSuccess), takeUntil(this.destroyed$))
      .subscribe((d) => {
        if (this.dialogRef) {
          this.dialogRef.close();
        }
        this.fetchPendingOwnerInvitations();
      });
  }

  ngOnInit(): void {
    this.pendingOwnerInvitations$ = this.store.select((store) => store.owners.invitations);
    this.fetchPendingOwnerInvitations();
  }

  fetchPendingOwnerInvitations() {
    const filteredParams = Utils.getFilteredParams(this.params);
    this.store.dispatch(loadPendingOwnerInvitations({ params: filteredParams }));
  }

  onUpdateStatus(updateStatusPayload: IUpdateInvitationStatus) {
    // const options = {
    //   message: this.getTranslatedString("confirm_status_change"),
    //   cancelText: this.getTranslatedString("common.cancel"),
    //   confirmText: this.getTranslatedString("common.yes"),
    // };
    // this.confirmDialog.open(options);
    // this.confirmDialog.confirmed().subscribe((confirmed) => {
    //   if (confirmed) {
    this.store.dispatch(updateInvitationStatus({ payload: updateStatusPayload }));
    this.fetchPendingOwnerInvitations();
    //   } else {
    //     this.invitedOwnersCmp.resetInitialStatus(updateStatusPayload.index);
    //   }
    // });
  }

  showInvitePropertyOwnerModal() {
    this.dialogRef = this.dialog.open(PropertyOwnerInvitationComponent, {
      id: "invitePropertyOwner",
      data: {},
    });
    this.dialogRef.componentInstance.invitePropertyOwnerClicked.subscribe((d) => {
      this.store.dispatch(invitePropertyOwner({ payload: d }));
    });
    this.dialogRef.afterClosed().subscribe((result) => {
      console.log("modal closed");
    });
  }
}
