import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { ActiveOwnersContainerComponent } from "./containers/active-owners-container/active-owners-container.component";
import { InvitedOwnersContainerComponent } from "./containers/invited-owners-container/invited-owners-container.component";
import { OwnersContainerComponent } from "./containers/owners-container/owners-container.component";

const routes: Routes = [
  {
    path: "",
    component: OwnersContainerComponent,
    children: [
      {
        path: "",
        redirectTo: "active",
        pathMatch: "full",
      },
      {
        path: "active",
        component: ActiveOwnersContainerComponent,
      },
      {
        path: "invited",
        component: InvitedOwnersContainerComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class OwnerRoutingModule {}
