import { Injectable, Injector } from "@angular/core";
import { ApiUrls } from "@core/enums";
import { BaseService } from "@core/services";
import { Observable } from "rxjs";
import { IUpdateInvitationStatus } from "./../interfaces/update-invitation-status";

@Injectable({
  providedIn: "root",
})
export class OwnersService extends BaseService {
  constructor(injector: Injector) {
    super(injector);
  }

  getActiveOwners(parameters: any): Observable<any> {
    return this.http.get(ApiUrls.ACTIVE_OWNERS, { params: parameters });
  }

  getPendingOwnerInvitations(parameters: any): Observable<any> {
    return this.http.get(ApiUrls.PROPERTY_OWNER_PENDING_INVITATIONS, { params: parameters });
  }

  updateInvitationStatus(updateInvitationStatus: IUpdateInvitationStatus) {
    return this.http.put(`${ApiUrls.UPDATE_INVITATION_STATUS}`, updateInvitationStatus);
  }

  getOwnerDetailsById(ownerId: string) {
    return this.http.get(`${ApiUrls.OWNER}/${ownerId}`);
  }
}
