import { Injectable } from "@angular/core";
import { SnackBarService } from "@core/services";
import { Actions, createEffect, Effect, ofType } from "@ngrx/effects";
import { TranslateService } from "@ngx-translate/core";
import { of } from "rxjs";
import { catchError, exhaustMap, map } from "rxjs/operators";
import * as OwnersActions from "../actions/owners.actions";
import { OwnersService } from "./../../services/owners.service";

@Injectable()
export class OwnersEffects {
  /**
   * Fetch all the active owners
   */
  loadActiveOwners$ = createEffect(() =>
    this.actions$.pipe(
      ofType(OwnersActions.loadActiveOwners),
      exhaustMap((action) =>
        this.service.getActiveOwners(action.params).pipe(
          map((response: any) => OwnersActions.loadActiveOwnersSuccess(response)),
          catchError((error) => of(OwnersActions.loadActiveOwnersFailure({ error })))
        )
      )
    )
  );

  loadPendingOwnerInvitations$ = createEffect(() =>
    this.actions$.pipe(
      ofType(OwnersActions.loadPendingOwnerInvitations),
      exhaustMap((action) =>
        this.service.getPendingOwnerInvitations(action.params).pipe(
          map((response: any) => OwnersActions.loadPendingOwnerInvitationsSuccess(response)),
          catchError((error) => of(OwnersActions.loadPendingOwnerInvitationsFailure({ error })))
        )
      )
    )
  );

  updateInvitationStatus$ = createEffect(() => {
    return this.actions$.pipe(
      ofType(OwnersActions.updateInvitationStatus),
      exhaustMap((action) =>
        this.service.updateInvitationStatus(action.payload).pipe(
          map((response: any) => OwnersActions.updateInvitationStatusSuccess(response)),
          catchError((error) => of(OwnersActions.updateInvitationStatusFailure({ error })))
        )
      )
    );
  });

  @Effect({ dispatch: false })
  updateInvitationStatusSuccess = this.actions$.pipe(
    ofType<any>(OwnersActions.updateInvitationStatusSuccess),
    map((invitation: any) => {
      this.snackBar.open(this.translate.instant("invitation_status_updated"));
    })
  );

  getOwnerDetails$ = createEffect(() => {
    return this.actions$.pipe(
      ofType(OwnersActions.getOwnerDetails),
      exhaustMap((action) =>
        this.service.getOwnerDetailsById(action.id).pipe(
          map((response: any) => OwnersActions.getOwnerDetailsSuccess(response)),
          catchError((error) => of(OwnersActions.getOwnerDetailsFailure({ error })))
        )
      )
    );
  });

  constructor(
    private actions$: Actions,
    private service: OwnersService,
    private snackBar: SnackBarService,
    private translate: TranslateService
  ) {}
}
